var vm = {
  name: "province",
  props: {
    show: {
      type: Boolean,
      default: false
    },
    list: {
      type: Array,
      default() {
        return [];
      }
    }
  },

  methods: {
    close() {
      this.$emit("close", false);
    },
    selArea(item){
      this.$emit('change',item)
    }
  }
};
export default vm;
